import React from 'react';
import chair from '../images/chair.png';


function BottomSection() {
    return (
      <section className="bottomsection" id="goals">
        <h2>Goals</h2>
        <img className="img-fluid" src={chair} alt="" width="500" class="mx-auto d-block" />
        <div className="row">
          <div className="col-sm-6">
            <h3>Professional</h3>
            <ul>
              <li>Continue learning new skills</li>
              <li>Work smart and give 100%</li>
              <li>Become an expert in my field</li>
              <li>Be a great communicator</li>
              <li>Mentor others</li>
            </ul>
          </div>
          <div className="col-sm-6">
            <h3>Personal</h3>
            <ul>
              <li>Raise responsible kids</li>
              <li>Cook delicious dinners</li>
              <li>Enjoy nature</li>
              <li>Be creative</li>
              <li>Read</li>
            </ul>
          </div>
        </div>
        <h5>The only way to do great work is to love what you do." - Steve Jobs</h5> 
      </section>
    )
  }

/* Some people may call me a nerd. I claim the label with pride.
 Bill Gates
Power comes not from knowledge kept but from knowledge shared.
 Bill Gates
Measuring programming progress by lines of code is like measuring aircraft building progress by weight.
 Bill Gates
The only way to do great work is to love what you do. If you haven't found it yet, keep looking. Don't settle.
 Steve Jobs
*/
  
export default BottomSection;